<template>
  <div class="mains">
    <div class="top">
      <div><label for="">请输入用户id  <input v-model.trim="userid" type="text" placeholder="请输入id查询"></label><button @click="search">查询</button></div>
      <div>
        <button @click="isMaskShow = !isMaskShow">添加用户</button>
      </div>
    </div>
    <div class="mask" v-if="isMaskShow">
      <div>
        <ul>
          <li>
            <label for="">
              请输入地址
              <input type="text" v-model="addUser.address">
            </label>
          </li>
          <li>
            <label for="">
              请输入学校
              <input type="text" v-model="addUser.school">
            </label>
          </li>
          <li>
            请输入性别
            <label for="">
             
              <input type="radio" value="0" name="sex" v-model="addUser.sex">
               男
            </label>
            <label for="">
              
              <input type="radio" value="1" name="sex" v-model="addUser.sex">
              女
            </label>
          </li>
          <li>
            <label for="">
              请输入名称
              <input type="text" v-model="addUser.name">
            </label>
          </li>
          <li>
            <label for="">
              请输入电话号码
              <input type="text" v-model="addUser.tel">
            </label>
          </li>
          <li>
            <label for="">
            请输入年龄
            <input type="text" v-model="addUser.age">
          </label>
          </li>
        </ul>
        <button @click="add" v-if="isUpdateShow">
          添加
        </button>
        <button @click="updates" v-else>
          修改
        </button>
        <button @click="del(addUser.id)"  v-if="isUpdateShow == false">
          删除当前用户
        </button>
      </div>
    </div>
    <table border="1"  cellspacing="0" cellpadding="0">
        <tr>
          <th>
            id
          </th>
          <th>
            地址
          </th>
          <th>
            添加时间
          </th>
          <th>
            学校
          </th>
          <th>
            性别
          </th>
          <th>
            名称
          </th>
          <th>
            电话号码
          </th>
          <th>
            年龄
          </th>
        </tr>
        <tr v-for="item in list" :key="item.id" @dblclick="updateUser(item)">
          <td>
            {{ item.id }}
          </td>
          <td>
            {{ item.address }}
          </td>
          <td>
            {{ item.addTime }}
          </td>
          <td>
            {{ item.school }}
          </td>
          <td>
            {{ item.sex }}
          </td>
          <td>
            {{ item.name }}
          </td>
          <td>
            {{ item.tel }}
          </td>
          <td>
            {{ item.age }}
          </td>
        </tr>
      </table>
      <div class="footer" v-if="isShow">
        总数: {{ total }}条
        <select name="" id="" v-model="limit" @change="init">
          <option value="5">5</option>
          <option value="10">10</option>
          <option value="20">20</option>
          <option value="30">30</option>
          <option value="40">40</option>
          <option value="50">50</option>
        </select>
        <div>
          <button @click="limits(index + 1)" v-for="(item , index) in totalPage" :key="index">
            {{ index + 1 }}
          </button>
        </div>
      </div>
    </div>
  
</template>
<script>
import mock from "mockjs";
import $ from 'jquery'
var timer ;
export default {
  data () {
    return {
      start:1,
      limit:5,
      total:"",
      totalPage:0,
      list:[],
      userid:"",
      isShow:true,
      isMaskShow:false,
      isUpdateShow:true,
      addUser:{
        address:"",
        school:"",
        sex:"",
        name:"",
        tel:"",
        age:"",
      }
    }
  },
  methods: {
    del(val){
      this.$ajax.post(`/user/del?id=${val}`)
      .then(res =>{
        //alert(res.data.code);
        this.isMaskShow = !this.isMaskShow;
        this.isUpdateShow = !this.isUpdateShow;
        this.init();
        //clearTimeout(timer);
      })
    },
    updateUser(val){
      this.isMaskShow = !this.isMaskShow;
      this.isUpdateShow = !this.isUpdateShow;
      this.addUser = val;
    },
    updates(){
      var data = this.addUser;
      this.$ajax.post(`/user/update`,data)
      .then(res =>{
        this.isMaskShow = !this.isMaskShow;
        this.isUpdateShow = !this.isUpdateShow;
        //alert(res.data.code);
        this.addUser = {
          address:"",
          school:"",
          sex:"",
          name:"",
          tel:"",
          age:"",
        }
      })
    },
    add(){
      var data = this.addUser;

      this.$ajax.post(`/user/save`,data)
      .then(res =>{
        this.isMaskShow = !this.isMaskShow;
        alert(res.data.code);
        this.init();
        this.addUser = {
          address:"",
          school:"",
          sex:"",
          name:"",
          tel:"",
          age:"",
        }
      })
      
    },
    search(){
      this.$ajax.get(`/user/detail?id=${this.userid}`)
      .then(res=>{
        var mes = res.data.data;
        this.list = [mes];
        this.isShow = false;
      })
    },
    init(val){
      this.$ajax.get(`/user/list?start=${this.start}&limit=${this.limit}`)
      .then(res=>{
        var mes = res.data.data;
        this.total = mes.totalRow;
        this.list = mes.list;
        this.totalPage = mes.totalPage
      })
    },
    limits(val){
      this.start = val;
      this.init()
    }
  },
  // updated () {
  //   var _this = this;
  //   for(let i = 0;i<_this.list.length;i++){
  //     timer = setTimeout(function(){
  //        _this.del(_this.list[i].id)
  //     },500)
     
  //   }
  // },
  created () {
    this.init();
    
  }
}
</script>

